.locations__backdrop {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: rgba(144, 196, 252, 0.742);

  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &.is-hidden{
    opacity: 0;
    pointer-events: none;
  }
}


.locations__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  z-index: 9999;
  padding: 15px;

  background-color: #ffb8ca;
  border-radius: 22px;
  box-shadow: 0 4px 4px rgb(0 0 0 / 25%);
}

.locations__modal-container {
  width: 275px;
  @include tablet {
    width: 500px;
  }

  @include desktop {
    width: 700px;
  }
}

.map {
  width: 100%;
}

.map__box {
  width: 100%;
  height: 400px;

  @include tablet {
    height: 450px;
  }

  @include desktop {
    height: 450px;
  }
}

.locations__text {
  text-align: center;
  font-family: $second-font;
  @include font(700, 16px, 1.5, 0.13em, $acent-color);
  margin-bottom: 5px;

  @include tablet {
    text-align: center;
    font-family: $second-font;
    font-size: 18px;
    margin-bottom: 5px;
  }

  @include desktop {
    font-size: 24px;
    margin-bottom: 20px;
  }
}
