.customer {
  background-color: $alternative-color;
  padding-top: 30px;
  padding-bottom: 30px;
  @include tablet{
    padding-bottom: 40px;
  }
  @include desktop {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.customer__wraper {
  padding-bottom: 28px;
  @include tablet {
    padding-bottom: 44px;
  }
}
.swiper-horizontal > .swiper-pagination-bullets {
  bottom: 0;
}
.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  border: none;
  border-radius: 50%;

  &:focus,
  &:checked {
    background-color: $alternative-color;
  }
}

.swiper-pagination-bullet-active {
  background-image: url(../images/customers_review/home.svg);
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 0;
  background-color: transparent;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.customer__border {
  border-radius: 50%;
  border: none;
  overflow: hidden;
}
.customer__img {
  width: 85px;
  height: 85px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;

  @include tablet {
    margin-bottom: 20px;
  }
  @include desktop {
    margin-bottom: 16px;
  }
}

.customer__card {
  position: relative;
  width: 100%;
  padding: 41px 50px 19px 50px;
  background-color: #fafafa;

  @include tablet {
    width: 620px;
    padding: 37px 6px 36px;
  }
  @include desktop {
    width: 968px;
    padding: 32px 74px 28px 72px;
  }
}

.customer__text {
  position: relative;
  z-index: 3;
  width: 100%;

  @include font(500, 12, 2.55, null, #000000);
  font-family: 'DM Sans';
  text-align: center;

  @include tablet {
    width: 514px;
    @include font(null, 14, 2.16, 0.04em, null);
  }

  @include desktop {
    width: 750px;
    @include font(null, 16, 1.86, 0.04em, null);
  }
}

.customer__gradient {
  padding-bottom: 3px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  background-image: repeating-linear-gradient(
    to bottom,
    #fafafa,
    #fafafa 30px,
    #e1e1e1 30px,
    #e1e1e1 31px
  );
  &::before {
    content: '';
    z-index: 2;
    position: absolute;
    top: 28px;
    left: 53px;
    height: 40px;
    width: 54px;

    background-image: url(../images/customers_review/qm.svg);
    background-repeat: no-repeat;
    background-size: cover;

    @include tablet {
      top: 20px;
      left: 6px;
      width: 60px;
    }

    @include desktop {
      top: 16px;
      left: 72px;
      width: 54px;
    }
  }

  @include tablet {
    width: 608px;
    padding-left: 51px;
  }
  @include desktop {
    width: 822px;
    padding-left: 36px;
  }
}

.customer__location {
  margin-top: 14px;
  font-family: 'DM Sans';
  @include font(500, 14, 1.86, null, #000000);
  text-align: center;
  display: grid;
  grid-template: 20px 1fr / 1fr;

  &::before {
    content: '\2022 \2022 \2022';
    display: flex;
    top: 215px;
    left: 127px;

    letter-spacing: 4px;
    height: 4px;
    flex-direction: column;
    justify-content: center;
    color: #c7133e;
  }

  @include tablet {
    margin-top: 4px;
    @include font(null, 16, null, null, null);
  }

  @include desktop {
    margin-top: 16px;
    @include font(null, 20, null, null, null);
  }
}
