h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  background: none;
  border: none;
  border-radius: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

:root {
  scroll-behavior: smooth;
  letter-spacing: .04em;
  color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.76;
}

.container {
  max-width: 480px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: none;
    min-width: none;
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.button {
  color: #fff;
  stroke: #fff;
  cursor: pointer;
  transition-property: background-color color;
  background-color: #d41443;
  border-radius: 22px;
  justify-content: center;
  align-items: baseline;
  column-gap: 8px;
  padding: 11px 37px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  text-decoration: none;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button--light {
  color: #d41443;
  stroke: #d41443;
  background-color: #fff;
}

.button--light:hover, .button--light:focus {
  color: #d41443;
  stroke: #d41443;
  background-color: #ecebeb;
}

.button-more {
  width: 18px;
  height: 18px;
  fill: #d41443;
  cursor: pointer;
  transition-property: background-color fill;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button-more:focus, .button-more:hover {
  fill: #fff;
  background-color: #d41443;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.header {
  width: 100%;
  height: 58px;
  z-index: 999;
  padding-top: 8px;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .header {
    height: 82px;
    padding-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
