.burger__backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background: rgba(225, 121, 145, .604);
  transition-property: opacity;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media screen and (min-width: 1200px) {
  .burger__backdrop {
    display: none;
  }
}

.burger {
  height: 100vh;
  width: 250px;
  background-color: #e17992;
  padding: 60px 0;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(0);
}

.burger::-webkit-scrollbar {
  width: 5px;
  transform: translateX(-5px);
}

.burger::-webkit-scrollbar-track {
  border-radius: 10px;
}

.burger::-webkit-scrollbar-thumb {
  background-color: #ffb8ca;
  border-radius: 10px;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.burger::-webkit-scrollbar-thumb:hover {
  background-color: #d41443;
}

.burger__backdrop.is-hidden .burger {
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .burger {
    width: 254px;
    padding-top: 58px;
  }
}

.menu-button--close {
